.samples {
  position: absolute;
  top: 0;
  left: 0;
  width: 1000px;
  height: 700px;
  display: none;
}

.show-samples .samples {
  display: block;
}

.samples .bar {
  height: 100px;
  width: 1000px;
  overflow: hidden;
  opacity: 0;
  -webkit-transition: opacity 0.1s;
  -moz-transition: opacity 0.1s;
  -o-transition: opacity 0.1s;
  -ms-transition: opacity 0.1s;
  transition: opacity 0.1s;

}

.show-bar .bar {
  -webkit-transition: opacity 1s;
  -moz-transition: opacity 1s;
  -o-transition: opacity 1s;
  -ms-transition: opacity 1s;
  transition: opacity 1s;
  opacity: 1;
}

.show-bar .details {
  display: none;
}

.show-bar .bookshelf {
  display: none;
}

.turnjs-slider {
  opacity: 0;
  -webkit-transition: opacity 0.1s;
  -moz-transition: opacity 0.1s;
  -o-transition: opacity 0.1s;
  -ms-transition: opacity 0.1s;
  transition: opacity 0.1s;
}

.show-bar .turnjs-slider {
  opacity: 1;
  -webkit-transition: opacity 1s;
  -moz-transition: opacity 1s;
  -o-transition: opacity 1s;
  -ms-transition: opacity 1s;
  transition: opacity 1s;
}

.samples .share {
  margin-top: 40px;
  text-align: center;
}

.samples .share .icon {
  margin: 0 20px;
  cursor: pointer;
}

.samples .quit {
  background-position: -44px 0;
  cursor: default;
  position: absolute;
  right: 0;
  top: 45px;
}

.samples .quit:hover {
  background-position: -66px 0;
}

