  margin: 20px auto;
}

.content {
  width: 1000px;
  margin: auto;
  font-family: "helvetica neue", helvetica, arial;
}

.fixed .content {
  padding-top: 60px;
}

.content section {
  font-size: 15px;
  color: #666;
}

.content li {
  padding: 2px;
  line-height: 30px;
}

.api-chart {
  width: 100%;
  float: left;
}

.api-chart:after {
  content: ".";
  clear: both;
  visibility: hidden;
}

.api-chart .column {
  width: 20%;
  float: left;
}

.api-chart h4 {
  font-weight: bold;
  color: #333;
  margin: 10px 0;
}

.api-chart ul {
  margin: 0;
  padding: 0;
}

.api-chart li {
  list-style: none;
}

.api-chart a {
  padding: 2px 10px;
  color: #666;
}

.api-chart a:hover {
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  border-radius: 20px;
  background: #ddd;
  text-decoration: none;
  color: #333;
}

.browser-support {
  line-height: 50px;
}

.browser-support:after {
  content: ".";
  clear: both;
